import { AbstractFilter } from './abstract_filter'

export class Ids extends AbstractFilter {

    private data: any = {};

    constructor(values: any[], type: string = '') {
        super();
        if (type) this.data.type = type;
        this.data.values = values;
    }

    public static make(values: any[], type: string = ''): Ids {
        return new Ids(values, type);
    }

    public toJSON() {
        return { ids: this.data };
    }
}